// Context type
interface SiteContentContextType {
  content: SiteContent
  updateContent: (section: keyof SiteContent, newContent: any) => void
  resetContent: () => void
  loadContent: (newContent: SiteContent) => void
}

// Create context
const SiteContentContext = createContext<SiteContentContextType | undefined>(undefined)

// Custom hook to use the context
export const useSiteContent = (): SiteContentContextType => {
  const context = useContext(SiteContentContext)
  if (context === undefined) {
    throw new Error('useSiteContent must be used within a SiteContentProvider')
  }
  return context
}

// Provider component
interface SiteContentProviderProps {
  children: ReactNode
}

export const SiteContentProvider: React.FC<SiteContentProviderProps> = ({ children }) => {
  const { loadContentFromAPI, saveContentToAPI } = useSiteContentAPI()

  // Initialize state with saved content or default
  const [content, setContent] = useState<SiteContent>(() => {
    try {
      const savedContent = localStorage.getItem('site_content')
      return savedContent ? JSON.parse(savedContent) : defaultContent
    } catch (error) {
      console.warn('Could not load saved content:', error)
      return defaultContent
    }
  })

  const [isInitialized, setIsInitialized] = useState(false)

  // Load content from API on mount
  useEffect(() => {
    const initializeContent = async () => {
      try {
        const apiContent = await loadContentFromAPI()
        if (apiContent) {
          console.log(' Contedo carregado da API')
          setContent(apiContent)
          localStorage.setItem('site_content', JSON.stringify(apiContent))
        } else {
          console.log(' Usando contedo local')
        }
      } catch (error) {
        console.warn('Erro ao carregar da API, usando localStorage:', error)
      } finally {
        setIsInitialized(true)
      }
    }

    initializeContent()
  }, [loadContentFromAPI])

  // Save to localStorage whenever content changes
  useEffect(() => {
    if (!isInitialized) return

    try {
      localStorage.setItem('site_content', JSON.stringify(content))
    } catch (error) {
      console.warn('Could not save content to localStorage:', error)
    }
  }, [content, isInitialized])

  // Update a specific section and save to API
  const updateContent = async (section: keyof SiteContent, newContent: any) => {
    const updatedContent = {
      ...content,
      [section]: newContent
    }

    setContent(updatedContent)

    // Save to API in background
    try {
      await saveContentToAPI(updatedContent)
      console.log(' Contedo sincronizado com API')
    } catch (error) {
      console.warn('Erro ao salvar na API (salvo localmente):', error)
    }
  }

  // Reset to default content
  const resetContent = async () => {
    setContent(defaultContent)

    // Save to API in background
    try {
      await saveContentToAPI(defaultContent)
    } catch (error) {
      console.warn('Erro ao resetar na API:', error)
    }
  }

  // Load complete new content (for imports)
  const loadContent = async (newContent: SiteContent) => {
    setContent(newContent)

    // Save to API in background
    try {
      await saveContentToAPI(newContent)
    } catch (error) {
      console.warn('Erro ao salvar contedo importado na API:', error)
    }
  }

  // Listen for storage changes from other tabs
  useEffect(() => {
    const handleStorageChange = (event: StorageEvent) => {
      if (event.key === 'site_content' && event.newValue) {
        try {
          const newContent = JSON.parse(event.newValue)
          setContent(newContent)
        } catch (error) {
          console.warn('Could not parse storage change:', error)
        }
      }
    }

    window.addEventListener('storage', handleStorageChange)
    return () => window.removeEventListener('storage', handleStorageChange)
  }, [])

  // Periodic sync with API to detect changes from other sessions
  useEffect(() => {
    if (!isInitialized) return

    const syncInterval = setInterval(async () => {
      try {
        const apiContent = await loadContentFromAPI()
        if (apiContent) {
          const currentContentStr = JSON.stringify(content)
          const apiContentStr = JSON.stringify(apiContent)

          if (currentContentStr !== apiContentStr) {
            console.log(' Contedo atualizado detectado na API, sincronizando...')
            setContent(apiContent)
            localStorage.setItem('site_content', apiContentStr)
          }
        }
      } catch (error) {
        console.warn(' Erro na sincronizao peridica:', error)
      }
    }, 10000) // Check every 10 seconds for better sync

    return () => clearInterval(syncInterval)
  }, [isInitialized, content, loadContentFromAPI])

  // Listen for window focus to sync immediately
  useEffect(() => {
    const handleFocus = async () => {
      try {
        const apiContent = await loadContentFromAPI()
        if (apiContent) {
          const currentContentStr = JSON.stringify(content)
          const apiContentStr = JSON.stringify(apiContent)

          if (currentContentStr !== apiContentStr) {
            console.log(' Sincronizando contedo ao focar janela...')
            setContent(apiContent)
            localStorage.setItem('site_content', apiContentStr)
          }
        }
      } catch (error) {
        // Silently ignore sync errors
      }
    }

    window.addEventListener('focus', handleFocus)
    return () => window.removeEventListener('focus', handleFocus)
  }, [content, loadContentFromAPI])

  const value: SiteContentContextType = {
    content,
    updateContent,
    resetContent,
    loadContent
  }

  return (
    <SiteContentContext.Provider value={value}>
      {children}
    </SiteContentContext.Provider>
  )
}

export default SiteContentContext