/**
 * Utilitrios para formatao de nmeros ordinais em portugus brasileiro
 * Suporta caracteres especiais como 1, 2, 3, 4, etc.
 */

/**
 * Converte um nmero para sua representao ordinal em portugus
 * @param num - Nmero a ser convertido
 * @param feminine - Se deve usar terminao feminina () ou masculina ()
 * @returns String com o nmero ordinal formatado
 */
export function formatOrdinalNumber(num: number, feminine: boolean = false): string {
  if (isNaN(num) || num < 1) {
    return num.toString()
  }

  const suffix = feminine ? '' : ''
  return `${num}${suffix}`
}

/**
 * Formata uma srie escolar com nmero ordinal correto
 * @param serie - Nome da srie (ex: "1 Ano", "2 Serie")
 * @returns Srie formatada com ordinal correto
 */
export function formatSerieWithOrdinal(serie: string): string {
  if (!serie) return serie

  // Padres para identificar sries e anos
  const patterns = [
    // "1 Ano" -> "1 Ano"
    { regex: /^(\d+)\s*(Ano|ano)(.*)$/i, feminine: false },
    // "2 Serie" -> "2 Srie"
    { regex: /^(\d+)\s*(Serie|srie|series|sries)(.*)$/i, feminine: true },
    // "3 Fundamental" -> "3 Fundamental"
    { regex: /^(\d+)\s*(Fundamental|fundamental)(.*)$/i, feminine: false },
    // "1 Mdio" -> "1 Mdio"
    { regex: /^(\d+)\s*(Mdio|medio|Medio)(.*)$/i, feminine: false }
  ]

  for (const pattern of patterns) {
    const match = serie.match(pattern.regex)
    if (match) {
      const number = parseInt(match[1])
      const term = match[2]
      const rest = match[3] || ''

      return `${formatOrdinalNumber(number, pattern.feminine)} ${term}${rest}`
    }
  }

  return serie
}

/**
 * Valida se um texto contm nmeros ordinais vlidos
 * @param text - Texto a ser validado
 * @returns true se contm ordinais vlidos
 */
export function hasValidOrdinals(text: string): boolean {
  const ordinalPattern = /\d+[]/g
  return ordinalPattern.test(text)
}

/**
 * Extrai nmeros ordinais de um texto
 * @param text - Texto para extrair ordinais
 * @returns Array com os nmeros ordinais encontrados
 */
export function extractOrdinals(text: string): string[] {
  const ordinalPattern = /\d+[]/g
  return text.match(ordinalPattern) || []
}

